/**news**/
.cc_news_container .read_more_news {
  display: block;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  color: #000;
  background: #ffffff;
  border: 1px solid #dddddd;
}
.each_container {
  background: #ffffff;
  margin-bottom: 10px;
}
.cc_news_notice_container {
  overflow: hidden;
  padding: 10px;
}
.cc_news_notice_container .thumbnail {
  float: left;
}
.cc_news_notice_container .news_content {
  overflow: hidden;
  margin-left: 215px;
}
.fie[v-cloak] {
  display: none;
}
ld-panel {
  position: absolute;
  right: 0;
  left: 0;
  bottom: -10px;
  text-align: center;
  margin: 10px 0;
}
.row-panel a,
.field-panel a {
  border: 1px solid #ccc;
  padding: 10px 14px;
  text-decoration: none;
  color: #111;
  display: inline-block;
  margin-bottom: 5px;
  border-radius: 0;
  margin-left: -5px;
  background: #ffffff;
}
.row-panel a:hover,
.field-panel a:hover {
  background: #015395;
  color: #ffffff;
}
.woopo_btn-add_row {
  border: 1px solid #ccc;
  padding: 10px 14px;
  text-decoration: none;
  color: #111;
  border-radius: 3px;
  display: inline-block;
  margin-bottom: 5px;
}
.woopo_btn-add_tab {
  border: 1px solid #ccc;
  padding: 5px 14px;
  text-decoration: none;
  color: #111;
  border-radius: 3px;
  display: inline-block;
  margin-bottom: 5px;
}
.woopo_center {
  text-align: center;
}
.woopo_btn-flat {
  border: 1px solid #ccc;
  padding: 5px 14px;
  text-decoration: none;
  color: #111;
  border-radius: 3px;
  display: inline-block;
}
.woopo_left {
  text-align: right;
}
.woopo_right {
  text-align: right;
}
.woopo_dib {
  display: inline-block;
}
.woopo_db {
  display: block;
}
.field-panel-list {
  position: absolute;
  z-index: 9999999;
  padding: 5px;
  right: 0;
  left: 0;
}
.field-panel-list a {
  position: absolute;
  right: 5px;
}
.field-panel-list ul {
  margin: 0;
  padding: 0;
}
.field-panel-list li {
  list-style: none;
  float: left;
  /*border: 1px solid #ddd;*/
  margin: 0;
  cursor: pointer;
  text-align: center;
  width: 20%;
  /*padding: 5px 10px;*/
  /*border-radius: 3px;*/
}
.field-panel-list li button {
  border-radius: 0;
  background: #015395;
  color: #FFFFFF;
  word-wrap: break-word;
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  display: block;
  width: 100%;
  /*border-radius: 0;
            background: #015395;
            color: #FFFFFF;*/
}
.field-panel-list li .field-panel-list-close:hover {
  background: #d71a1a;
  color: #FFFFFF;
}
.field-panel-list li:hover {
  /*background: #cccccc;
            color: #ffffff;*/
}
.el-button {
  text-decoration: none;
}
/*** Edit ***/
.woopo_form_builder_panel {
  padding-bottom: 500px;
}
.woopo-edit-mode {
  position: relative;
  z-index: 0;
}
.wpfront-edit-mode .focused_row {
  border: 1px solid #CCCCCC;
  margin-bottom: 10px;
}
.wpfront-edit-mode .focused_row .el-row {
  padding: 5px;
  min-height: 50px;
  margin-top: 5px;
  margin-bottom: 5px;
  padding-bottom: 50px;
}
.wpfront-edit-mode .focused_row .input-col {
  position: relative;
  z-index: 999999;
}
.wpfront-edit-mode .focused_row .input-col a {
  text-decoration: none;
  padding: 5px 11px;
  background: #333;
  color: #fff;
}
.woopo_input_panel {
  position: absolute;
  text-align: center;
  right: 0;
  left: 0;
  bottom: -15px;
  z-index: 99999;
}
.woopo_input_panel a {
  /*padding: 5px 15px;*/
  display: inline;
  border-radius: 0;
  margin-left: -3px;
}
.woopo_input_panel .woopo_grid_panel {
  position: absolute;
  right: 0;
  left: 0;
  bottom: -20px;
  background: #ffffff;
  padding: 5px;
  border: 1px solid #ddd;
  display: inline;
  text-align: center;
}
.woopo_input_panel .woopo_grid_panel a {
  padding: 5px 10px;
  display: inline-block;
  margin: 2px;
  border: 1px solid #dddddd;
}
/**custom**/
.woopo_entry_single_view table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100% !important;
  border: 1px solid #ddd !important;
}
.woopo_entry_single_view th,
.woopo_entry_single_view td {
  text-align: left;
  padding: 16px;
}
.woopo_entry_single_view tr:nth-child(even) {
  background-color: #f2f2f2;
}
.woopo_form_type h4 {
  margin-bottom: 10px;
}
.woopo_form_type h5 {
  margin-top: 0px;
  margin-bottom: 20px;
}
.wpfront-field-settings-panel {
  text-align: left;
  position: absolute;
  right: 0;
  left: 0;
  padding: 10px;
  overflow: hidden;
  margin-top: 5px;
}
.wpfront-field-settings-panel .el-form-item {
  margin-bottom: 0;
}
.wpfront-field-settings-panel .wpfront-field-settings-action {
  text-align: right;
  display: block;
}
.wpfront-field-settings-panel .el-form-item__content {
  padding-right: 5px;
}
.woopo_tab-panel {
  width: 500px;
  position: absolute;
  z-index: 999999;
  left: 33%;
}
.cc-edit-panel {
  max-height: 400px;
  overflow-y: scroll;
}
.cc-edit-panel .el-col-24 {
  margin-bottom: 20px;
  padding: 0 10px;
}
.cc-edit-panel .el-input__inner {
  margin-top: 10px;
}
.cc-edit-panel .el-row {
  padding: 0;
  min-height: auto;
  margin-top: 0;
  margin-bottom: 0;
}
.panel-repeatable {
  border: 1px solid #dddddd;
  padding: 5px;
}
.wpfront-menu a {
  text-decoration: none;
}
.wpfront-menu .is-active {
  background: #409EFF !important;
  color: #ffffff !important;
}
.pro_fields {
  border: 1px solid #cc0000;
  padding: 5px;
  color: #cc0000;
  padding: 8px 10px !important;
  border-radius: 5px;
}
.pro_fields p.description {
  color: #cc0000;
}
/*common*/
.woopo_layout_cornered input[type="text"],
.woopo_layout_cornered input[type="email"],
.woopo_layout_cornered input[type="url"],
.woopo_layout_cornered input[type="password"],
.woopo_layout_cornered input[type="search"],
.woopo_layout_cornered input[type="number"],
.woopo_layout_cornered input[type="tel"],
.woopo_layout_cornered input[type="range"],
.woopo_layout_cornered input[type="date"],
.woopo_layout_cornered input[type="month"],
.woopo_layout_cornered input[type="week"],
.woopo_layout_cornered input[type="time"],
.woopo_layout_cornered input[type="datetime"],
.woopo_layout_cornered input[type="datetime-local"],
.woopo_layout_cornered input[type="color"],
.woopo_layout_cornered textarea {
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  border-radius: 0px;
}
.active_col_btn {
  background: #fff !important;
  color: #000 !important;
  border: 2px solid #000 !important;
}
.woopo_tabs .tabs {
  background: #015395;
}
.woopo_tabs .tabs a {
  padding: 15px 10px;
  display: block;
  /*float: left;*/
  text-align: center;
  font-weight: bold;
  font-size: 15px;
  text-decoration: none;
  color: #fff;
}
.woopo_tabs .tabs a:hover {
  background: #ecf5ff;
  color: #3a8ee6;
}
.woopo_tabs .tab_cotnent {
  background: #FFFFFF;
}
.woopo_element_tree .woopo_tree_tab .woopo_tree_tab_span {
  display: block;
  background: #555;
  padding: 10px 10px;
  color: #fff;
}
.woopo_element_tree .woopo_tree_tab .woopo_tree_tab_span a {
  text-decoration: none;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  padding: 0px 5px 2px 5px;
  right: 10px;
}
.woopo_element_tree .woopo_tree_tab .woopo_tree_placeholder {
  margin-left: 10px;
}
.woopo_element_tree .woopo_tree_placeholder .woopo_tree_placeholder_span {
  display: block;
  background: #555;
  padding: 10px 10px;
  color: #fff;
  margin-top: 3px;
}
.woopo_element_tree .woopo_tree_placeholder .woopo_tree_placeholder_span a {
  text-decoration: none;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  padding: 0px 5px 2px 5px;
  right: 10px;
}
.woopo_element_tree li.woopo_tree_field {
  margin-left: 10px;
}
.woopo_element_tree li.woopo_tree_field .woopo_tree_field_span {
  border: 1px solid #015395;
  display: block;
  padding: 5px 10px;
  margin-top: 3px;
}
.woopo_element_tree li.woopo_tree_field .woopo_tree_field_span a {
  text-decoration: none;
  border: 1px solid #015395;
  padding: 0px 5px 2px 5px;
  right: 10px;
}
.woopo_element_tree li.woopo_tree_field .woopo_tree_field_settings {
  border: 1px solid #ccc;
  padding: 0 10px;
}
.action_panel {
  padding: 10px;
  background: #fff;
}
.wpfront-form-types .form-create {
  background: #015395;
  border-color: #015395;
}
/*
Generic Styling, for Desktops/Laptops
*/
.woopo-section-posts {
  /* Zebra striping */
}
.woopo-section-posts table {
  width: 100%;
  border-collapse: collapse;
}
.wpfrontend-section-posts tr:nth-of-type(odd) {
  background: #eee;
}
.wpfrontend-section-posts th {
  background: #333;
  color: white;
  font-weight: bold;
}
.wpfrontend-section-posts td,
.wpfrontend-section-posts th {
  padding: 6px;
  border: 1px solid #ccc;
  text-align: left;
}
/**dashboard**/
#wpfrontend-public-dashboard .dashboard-nav {
  width: 20%;
}
#wpfrontend-public-dashboard .dashboard-nav ul {
  list-style-type: none;
}
#wpfrontend-public-dashboard .dashboard-nav ul li.active a {
  color: #aaaaaa;
}
#wpfrontend-public-dashboard .dashboard-content {
  width: 80%;
}
